.resultLotto-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    height: 100vh;
    max-height: 800px;
    max-width: 420px;
    margin: 0 auto;

    .home {
        position: absolute;
        left: 10px;
        top: 10px;
        cursor: pointer;
    }

    .win-title {
        font-weight: bold;
        font-size: 45px;
        color: #5b6860;
        font-family: 'Shalimar', 'Franklin Gothic Medium', 'Courier New', Courier, monospace;
        margin-top: 80px;
        margin-bottom: 50px;
    }

    .win-section {
        display: flex;
        flex-direction: column;

        .win-list {
            display: flex;
            align-items: center;
            margin-bottom: 30px;

            .win-list__idx {
                display: flex;
                align-items: center;
                font-family: 'Roboto', sans-serif;
                font-weight: bold;

                .win-list__idx__border {
                    display: inline-block;
                    margin: 0 7px;
                    width: 0.1px;
                    background-color: rgb(94, 94, 94);
                    height: 30px;
                    opacity: 0.2;
                }
            }

            .win-list__number-container {
                display: flex;

                .win-number {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 36px;
                    height: 36px;
                    border-radius: 0.4em;
                    background-color: rgba(142, 149, 152, 0.3);
                    font-weight: bold;
                    font-size: 16px;
                    margin: 0 5px;

                    &:first-child {
                        margin-right: 5px;
                        margin-left: 0px;
                    }

                    &:last-child {
                        margin-left: 5px;
                        margin-right: 7px;
                    }
                }
            }
        }
    }

    .submit-container {
        position: absolute;
        width: 80px;
        height: 30px;
        left: 50%;
        bottom: 8%;
        transform: translateX(-50%);
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border-radius: 6px;
        background-color: #c6c6c6;
        font-family: 'Shalimar',
            'Franklin Gothic Medium',
            'Courier New',
            Courier,
            monospace;
        font-weight: bold;
        color: black;
    }
}